if (module.hot) {
  module.hot.accept();
}

import { formatDistanceToNow } from "date-fns";

class Priority {
  static NORMAL = 0;
  static IMPORTANT = 1;
  static URGENT = 2;

  static isValid(priority) {
    return (
      [Priority.NORMAL, Priority.IMPORTANT, Priority.URGENT].includes(
        priority,
      ) || Object.keys(Priority).includes(priority)
    );
  }

  static asString(value) {
    if (typeof value === "string" && Priority.isValid(value)) {
      return value;
    } else if (Object.values(Priority).includes(value)) {
      return Object.keys(Priority).reduce(function (acc, key) {
        return ((acc[Priority[key]] = key), acc);
      }, {})[value];
    } else return "NORMAL";
  }

  static asValue(str) {
    if (Object.keys(Priority).includes(str)) {
      return Priority[str];
    } else return 0;
  }
}

class TaskList {
  title = "";
  desc = "";
  priority = "";
  category = "";
  duedate = new Date();
  taskGroups = [];

  id = "";

  constructor(title, duedate, priority = 0, category = "") {
    if (typeof title == "object") {
      if (Object.hasOwn(title, "title")) this.title = title.title;
      else return undefined;
      if (Object.hasOwn(title, "desc")) this.desc = title.desc;
      else return undefined;
      if (Object.hasOwn(title, "priority")) this.priority = title.priority;
      else return undefined;
      if (Object.hasOwn(title, "category")) this.category = title.category;
      else return undefined;
      if (Object.hasOwn(title, "duedate"))
        this.duedate = new Date(title.duedate);
      else return undefined;
      if (Object.hasOwn(title, "taskGroups"))
        this.taskGroups = title.taskGroups.map((group) =>
          TaskGroup.parseJSON(group),
        );
      else return undefined;
      if (Object.hasOwn(title, "id")) this.id = title.id;
      else return undefined;
    } else {
      this.title = title;
      this.duedate = new Date(duedate);
      this.priority = Priority.asString(priority);
      this.category = category;

      this.taskGroups.push(new TaskGroup("tasks"));
      this.addTask(0, "Edit this task to get started!");

      this.id = crypto.randomUUID();
    }
  }

  toggleTaskCheck(group, index) {
    this.taskGroups[group].toggleTaskCheck(index);
    this.save();
  }

  removeTask(group, index) {
    this.taskGroups[group].removeTask(index);
  }

  addTask(group, desc) {
    this.taskGroups[group].addTask(new Task(desc));
  }

  editTask(group, index, text) {
    this.taskGroups[group].tasks[index].desc = text;
  }

  removeGroup(group) {
    this.taskGroups.splice(group, 1);
  }

  addGroup(title) {
    this.taskGroups.push(new TaskGroup(title));
  }

  editGroup(group, text) {
    this.taskGroups[group].title = text;
  }

  getGroupNames() {
    return this.taskGroups.map((group) => group.title);
  }

  isTaskGroupFinished(index) {
    return this.taskGroups[index].areTasksFinished();
  }

  areTasksFinished() {
    return this.taskGroups.every((group) => group.areTasksFinished());
  }

  getFormattedDueDate() {
    return this.duedate.toLocaleDateString(navigator.language, {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  }

  getRemainingTime() {
    return formatDistanceToNow(this.duedate);
  }

  getTotalTasks() {
    return this.taskGroups.reduce(
      (total, current) => total + current.tasks.length,
      0,
    );
  }

  getTotalTasksCompleted() {
    return this.taskGroups.reduce(
      (total, current) =>
        total + current.tasks.filter((task) => task.isFinished).length,
      0,
    );
  }

  get id() {
    return this.id;
  }

  save() {
    localStorage.setItem(this.id, JSON.stringify(this));
  }
}

class TaskGroup {
  title = "";
  tasks = [];

  constructor(title) {
    this.title = title;
  }

  static parseJSON(group) {
    let g = new TaskGroup(group.title);

    g.tasks = group.tasks.map((t) => Task.parseJSON(t));
    return g;
  }

  toggleTaskCheck(index) {
    this.tasks[index].toggleCheck();
  }

  removeTask(index) {
    this.tasks.splice(index, 1);
  }

  addTask(task) {
    this.tasks.push(task);
  }

  areTasksFinished() {
    return this.tasks.every((task) => task.isFinished);
  }
}

class Task {
  desc = "";
  isFinished = false;

  constructor(desc) {
    this.desc = desc;
  }

  static parseJSON(task) {
    let t = new Task(task.desc);
    if (task.isFinished) {
      t.toggleCheck();
    }

    return t;
  }

  toggleCheck() {
    this.isFinished = !this.isFinished;
  }
}

export { TaskList, TaskGroup, Task, Priority };
