___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}

body {
    padding: 0px;
    margin: 0px;
}

#page {
    max-width: 100vw;

    display: flex;
    align-items: flex-start;
    overflow: hidden;
}

.flex-spacer {
    flex: 1 auto;
}

/* Sidebar */
#sidebar {
    flex: 0 auto;

    height: 100vh;
    max-width: 0px;
    z-index: 1;

    padding: 0px;

    background-color: burlywood;

    overflow-x: hidden;
    overflow-y: scroll;
    text-wrap: nowrap;

    display: flex;
    flex-direction: column;

    transition-duration: 150ms;
    transition-property: width, max-width;
}

#sidebar.sidebarOpen {
    max-width: 250px;
}

#tasklists {
    padding: 16px 0px;
}

#quickbar {
    flex: 0 100px;
    width: 250px;

    background-color: gainsboro;

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
    gap: 1px;

    text-wrap: wrap;

    button {
        background-color: ghostwhite;

        transition: background-size 150ms ease-in-out;
    }

    button:hover {
        background-size: 28px;
    }

    button:active {
        background-size: 20px;
    }
}

#new-list {
    grid-area: 1 / 1 / span 1 / span 4;
}

#tasklists .sidebar-entry {
    border: 0px;
    border-radius: 12px 0px 0px 12px;

    background-color: transparent;
    background-clip: padding-box;

    text-align: left;
    font-size: 1rem;
}

#tasklists .sidebar-entry:active {
    background-color: white;

    transition: 150ms;
}

#category-lists {
    margin-top: 16px;
    margin-bottom: 8px;
}

.list-entry {
    padding: 4px 0px 4px 4px;

    display: flex;
    align-items: stretch;
}

.list-entry button {
    padding: 4px 0px 4px 12px;

    flex: 1 auto;
}

.priority-filter,
.category-header {
    font-weight: bold;
}

#category-lists .list-entry,
.category-content {
    display: flex;
    flex-direction: column;

    align-items: stretch;

    text-align: left;
}

.category-entry {
    padding: 4px 4px 4px 24px;
}

.category-content {
    padding-left: 12px;

    transition: max-height 150ms;
}

.category-content.content-close {
    overflow: hidden;

    max-height: 0px;
}

/* Main Section*/
#main {
    flex: 1;

    display: flex;
    flex-direction: column;
}

/* Top Bar */
#topbar {
    padding: 6px 24px;

    flex: 1;
    align-self: stretch;

    border-bottom: 1px solid dimgray;

    display: flex;
    align-items: center;
    gap: 12px;
}

#topbar h1 {
    margin: 6px;
}

#toggle-sidebar {
    flex: 0 24px;
    align-self: stretch;

    border: 0px;
    background-color: transparent;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});

    background-size: 48px;
    background-position: center;
    background-repeat: no-repeat;
}

#quick-action {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    button {
        padding: 24px;
        border: 0px;
        background-color: transparent;

        transition: scale 150ms ease-in-out;
    }

    button:hover {
        scale: 1.1;
    }

    button:active {
        scale: 0.9;
    }
}

/* Content */
#content {
    align-self: stretch;

    margin-top: 24px;

    display: flex;
    justify-content: center;
}

#task-list {
    flex: 0 750px;
}

#details {
    display: flex;
}

#title {
    flex: 1 auto;

    display: flex;
    flex-direction: column;

    h1 {
        margin: 0px 0px 8px 0px;
    }

    .priority {
        align-self: flex-start;
    }
}

.priority {
    padding: 4px 16px;
    border-radius: 16px;

    font-size: 0.8rem;
    font-weight: bold;

    background-color: midnightblue;
    color: white;
}

.priority.important {
    background-color: burlywood;
}

.priority.urgent {
    background-color: red;
}

#due {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;

    text-align: right;
}

#due-remain {
    align-self: self-end;

    span {
        padding: 0px 4px;
        margin-left: 4px;
    }
}

#duedate {
    white-space: pre;
}

.task-group,
.task {
    margin-left: 40px;
}

.task-group {
    margin-top: 24px;
}

.task-check:checked + span,
.finished {
    text-decoration: line-through;
}

.context-container {
    position: relative;
}

.property-actions {
    display: none;

    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
}

.context-container:hover > .property-actions {
    display: flex;

    gap: 4px;
}

.property-action {
    display: inherit;
    height: 32px;
    aspect-ratio: 1/1;
    border: 1px solid gainsboro;
    border-radius: 50%;
    z-index: 2;

    background-color: white;

    transition-duration: 150ms;
    transition-property: height, margin;
}

.property-action:active {
    margin: 1px;
    height: 30px;
}

.category-entry .property-actions {
    right: 24px;
}

/* Dialogs/Popup Form */
dialog {
    border: 0px;
    border-radius: 16px;
    outline: none;
}

dialog::backdrop {
    background-color: gainsboro;
    opacity: 75%;
}

#new-list-dialog > form {
    width: 50vw;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template: "h1 h1" "l1 l1" "ti ti" "l2 l3" "dd pr" "l4 ." "cg ." "bt bt";
    column-gap: 16px;

    label {
        margin-bottom: 4px;
        font-size: 1.2rem;
    }

    input,
    select {
        margin-bottom: 12px;
    }
}

#new-list-dialog h2 {
    margin-top: 4px;

    grid-area: h1;
}

#new-list-dialog input,
#new-list-dialog select,
#new-list-dialog button {
    font-size: 1.1rem;

    padding: 4px;
    border-radius: 8px;
}

#label-title {
    grid-area: l1;
}

#input-title {
    grid-area: ti;
}

#label-duedate {
    grid-area: l2;
}

#input-duedate {
    grid-area: dd;
}

#label-priority {
    grid-area: l3;
}

#input-priority {
    grid-area: pr;
}

#label-category {
    grid-area: l4;
}

#input-category {
    grid-area: cg;
}

#new-list-submit {
    grid-area: bt;
    text-align: center;

    margin-top: 24px;

    button {
        padding: 8px 16px;
    }
}

#edit-text-dialog > form,
#new-group-dialog > form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;

    font-size: 1.1rem;
}

#edit-text-dialog textarea {
    width: 25vw;
}

#edit-text-dialog button,
#new-group-dialog button {
    margin-top: 8px;
}

#new-task-dialog > form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;
}

#new-task-dialog select {
    width: 100%;
}

#new-task-submit {
    margin: 0 auto;
    margin-top: 8px;
}

/* Category View*/
#list-overview {
    flex: 1;
    align-self: stretch;

    padding: 0px 16px;

    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-template-rows: repeat(auto-fit, 200px);
    gap: 16px;
}

.overview-entry {
    display: flex;

    transition: scale 150ms ease-in-out;

    .property-actions {
        top: 6px;
        right: 6px;
    }
}

.overview-entry:hover {
    scale: 1.05;
}

.overview-entry:active {
    scale: 0.9;
}

.overview-details {
    flex: 1 auto;

    padding: 8px;
    border-radius: 16px;
    border: 3px solid gainsboro;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr auto;
    grid-auto-columns: 1fr;
    grid-auto-rows: auto;
    gap: 8px;

    overflow: hidden;

    .priority,
    .overview-due {
        text-align: center;
    }
}

.overview-title {
    grid-area: 1 / 1 / span 1 / span 2;

    margin: 3px;

    font-size: 1.4rem;
    font-weight: bold;
}

.completion {
    grid-area: 2 / 1 / span 1 / span 2;

    margin: 0 auto;
}

.overview-details > .priority {
    grid-area: 3 / 1 / span 1 / span 1;

    justify-self: center;
    align-self: center;
}

.overview-due {
    grid-area: 3 / 2 / span 1 / span 1;

    margin: 0px -8px -8px 0px;

    border-top-left-radius: 24px;

    background-color: gainsboro;

    display: flex;
    justify-content: center;
    align-items: center;
}

/* Hide */
.hide {
    display: none !important;
}

/* Icons */
.image-button {
    border: 0px;
    background-color: transparent;

    background-position: center;
    background-repeat: no-repeat;
}

#new-task {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: 36px;
}

#new-group {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: 36px;
}

#preferences {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: 36px;
}

.edit {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    background-size: 24px;

    background-position: center;
    background-repeat: no-repeat;
}

.remove {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

#new-list {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
    background-size: 24px;
}

#import {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
    background-size: 24px;
}

#export {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
    background-size: 24px;
}

#sync {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
    background-size: 24px;
}

#settings {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
    background-size: 24px;
}
