    transition: 150ms;\n}\n\n#category-lists {\n    margin-top: 16px;\n    margin-bottom: 8px;\n}\n\n.list-entry {\n    padding: 4px 0px 4px 4px;\n\n    display: flex;\n    align-items: stretch;\n}\n\n.list-entry button {\n    padding: 4px 0px 4px 12px;\n\n    flex: 1 auto;\n}\n\n.priority-filter,\n.category-header {\n    font-weight: bold;\n}\n\n#category-lists .list-entry,\n.category-content {\n    display: flex;\n    flex-direction: column;\n\n    align-items: stretch;\n\n    text-align: left;\n}\n\n.category-entry {\n    padding: 4px 4px 4px 24px;\n}\n\n.category-content {\n    padding-left: 12px;\n\n    transition: max-height 150ms;\n}\n\n.category-content.content-close {\n    overflow: hidden;\n\n    max-height: 0px;\n}\n\n/* Main Section*/\n#main {\n    flex: 1;\n\n    display: flex;\n    flex-direction: column;\n}\n\n/* Top Bar */\n#topbar {\n    padding: 6px 24px;\n\n    flex: 1;\n    align-self: stretch;\n\n    border-bottom: 1px solid dimgray;\n\n    display: flex;\n    align-items: center;\n    gap: 12px;\n}\n\n#topbar h1 {\n    margin: 6px;\n}\n\n#toggle-sidebar {\n    flex: 0 24px;\n    align-self: stretch;\n\n    border: 0px;\n    background-color: transparent;\n\n    background-image: url(\"../assets/menu.svg\");\n\n    background-size: 48px;\n    background-position: center;\n    background-repeat: no-repeat;\n}\n\n#quick-action {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-around;\n\n    button {\n        padding: 24px;\n        border: 0px;\n        background-color: transparent;\n\n        transition: scale 150ms ease-in-out;\n    }\n\n    button:hover {\n        scale: 1.1;\n    }\n\n    button:active {\n        scale: 0.9;\n    }\n}\n\n/* Content */\n#content {\n    align-self: stretch;\n\n    margin-top: 24px;\n\n    display: flex;\n    justify-content: center;\n}\n\n#task-list {\n    flex: 0 750px;\n}\n\n#details {\n    display: flex;\n}\n\n#title {\n    flex: 1 auto;\n\n    display: flex;\n    flex-direction: column;\n\n    h1 {\n        margin: 0px 0px 8px 0px;\n    }\n\n    .priority {\n        align-self: flex-start;\n    }\n}\n\n.priority {\n    padding: 4px 16px;\n    border-radius: 16px;\n\n    font-size: 0.8rem;\n    font-weight: bold;\n\n    background-color: midnightblue;\n    color: white;\n}\n\n.priority.important {\n    background-color: burlywood;\n}\n\n.priority.urgent {\n    background-color: red;\n}\n\n#due {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: flex-end;\n\n    text-align: right;\n}\n\n#due-remain {\n    align-self: self-end;\n\n    span {\n        padding: 0px 4px;\n        margin-left: 4px;\n    }\n}\n\n#duedate {\n    white-space: pre;\n}\n\n.task-group,\n.task {\n    margin-left: 40px;\n}\n\n.task-group {\n    margin-top: 24px;\n}\n\n.task-check:checked + span,\n.finished {\n    text-decoration: line-through;\n}\n\n.context-container {\n    position: relative;\n}\n\n.property-actions {\n    display: none;\n\n    position: absolute;\n    top: 50%;\n    right: 0;\n    transform: translate(50%, -50%);\n}\n\n.context-container:hover > .property-actions {\n    display: flex;\n\n    gap: 4px;\n}\n\n.property-action {\n    display: inherit;\n    height: 32px;\n    aspect-ratio: 1/1;\n    border: 1px solid gainsboro;\n    border-radius: 50%;\n    z-index: 2;\n\n    background-color: white;\n\n    transition-duration: 150ms;\n    transition-property: height, margin;\n}\n\n.property-action:active {\n    margin: 1px;\n    height: 30px;\n}\n\n.category-entry .property-actions {\n    right: 24px;\n}\n\n/* Dialogs/Popup Form */\ndialog {\n    border: 0px;\n    border-radius: 16px;\n    outline: none;\n}\n\ndialog::backdrop {\n    background-color: gainsboro;\n    opacity: 75%;\n}\n\n#new-list-dialog > form {\n    width: 50vw;\n\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-template-rows: 1fr;\n    grid-template: \"h1 h1\" \"l1 l1\" \"ti ti\" \"l2 l3\" \"dd pr\" \"l4 .\" \"cg .\" \"bt bt\";\n    column-gap: 16px;\n\n    label {\n        margin-bottom: 4px;\n        font-size: 1.2rem;\n    }\n\n    input,\n    select {\n        margin-bottom: 12px;\n    }\n}\n\n#new-list-dialog h2 {\n    margin-top: 4px;\n\n    grid-area: h1;\n}\n\n#new-list-dialog input,\n#new-list-dialog select,\n#new-list-dialog button {\n    font-size: 1.1rem;\n\n    padding: 4px;\n    border-radius: 8px;\n}\n\n#label-title {\n    grid-area: l1;\n}\n\n#input-title {\n    grid-area: ti;\n}\n\n#label-duedate {\n    grid-area: l2;\n}\n\n#input-duedate {\n    grid-area: dd;\n}\n\n#label-priority {\n    grid-area: l3;\n}\n\n#input-priority {\n    grid-area: pr;\n}\n\n#label-category {\n    grid-area: l4;\n}\n\n#input-category {\n    grid-area: cg;\n}\n\n#new-list-submit {\n    grid-area: bt;\n    text-align: center;\n\n    margin-top: 24px;\n\n    button {\n        padding: 8px 16px;\n    }\n}\n\n#edit-text-dialog > form,\n#new-group-dialog > form {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 8px;\n\n    font-size: 1.1rem;\n}\n\n#edit-text-dialog textarea {\n    width: 25vw;\n}\n\n#edit-text-dialog button,\n#new-group-dialog button {\n    margin-top: 8px;\n}\n\n#new-task-dialog > form {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 8px;\n}\n\n#new-task-dialog select {\n    width: 100%;\n}\n\n#new-task-submit {\n    margin: 0 auto;\n    margin-top: 8px;\n}\n\n/* Category View*/\n#list-overview {\n    flex: 1;\n    align-self: stretch;\n\n    padding: 0px 16px;\n\n    display: grid;\n    grid-template-columns: repeat(auto-fit, 300px);\n    grid-template-rows: repeat(auto-fit, 200px);\n    gap: 16px;\n}\n\n.overview-entry {\n    display: flex;\n\n    transition: scale 150ms ease-in-out;\n\n    .property-actions {\n        top: 6px;\n        right: 6px;\n    }\n}\n\n.overview-entry:hover {\n    scale: 1.05;\n}\n\n.overview-entry:active {\n    scale: 0.9;\n}\n\n.overview-details {\n    flex: 1 auto;\n\n    padding: 8px;\n    border-radius: 16px;\n    border: 3px solid gainsboro;\n\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-template-rows: 1fr 1fr auto;\n    grid-auto-columns: 1fr;\n    grid-auto-rows: auto;\n    gap: 8px;\n\n    overflow: hidden;\n\n    .priority,\n    .overview-due {\n        text-align: center;\n    }\n}\n\n.overview-title {\n    grid-area: 1 / 1 / span 1 / span 2;\n\n    margin: 3px;\n\n    font-size: 1.4rem;\n    font-weight: bold;\n}\n\n.completion {\n    grid-area: 2 / 1 / span 1 / span 2;\n\n    margin: 0 auto;\n}\n\n.overview-details > .priority {\n    grid-area: 3 / 1 / span 1 / span 1;\n\n    justify-self: center;\n    align-self: center;\n}\n\n.overview-due {\n    grid-area: 3 / 2 / span 1 / span 1;\n\n    margin: 0px -8px -8px 0px;\n\n    border-top-left-radius: 24px;\n\n    background-color: gainsboro;\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n/* Hide */\n.hide {\n    display: none !important;\n}\n\n/* Icons */\n.image-button {\n    border: 0px;\n    background-color: transparent;\n\n    background-position: center;\n    background-repeat: no-repeat;\n}\n\n#new-task {\n    background-image: url(\"../assets/note-plus-outline.svg\");\n    background-size: 36px;\n}\n\n#new-group {\n    background-image: url(\"../assets/folder-plus-outline.svg\");\n    background-size: 36px;\n}\n\n#preferences {\n    background-image: url(\"../assets/application-cog-outline.svg\");\n    background-size: 36px;\n}\n\n.edit {\n    background-image: url(\"../assets/pencil.svg\");\n    background-size: 24px;\n\n    background-position: center;\n    background-repeat: no-repeat;\n}\n\n.remove {\n    background-image: url(\"../assets/minus.svg\");\n}\n\n#new-list {\n    background-image: url(\"../assets/receipt-text-plus.svg\");\n    background-size: 24px;\n}\n\n#import {\n    background-image: url(\"../assets/import.svg\");\n    background-size: 24px;\n}\n\n#export {\n    background-image: url(\"../assets/export.svg\");\n    background-size: 24px;\n}\n\n#sync {\n    background-image: url(\"../assets/calendar-sync.svg\");\n    background-size: 24px;\n}\n\n#settings {\n    background-image: url(\"../assets/cog.svg\");\n    background-size: 24px;\n}\n"],"sourceRoot":""}]);
